import React ,{useState}from 'react'
import classes from './NewContact.module.css';
import ContactForm from './ContactForm';

const NewContact = (props) => {
    const [isEditing, setIsEditing] = useState(false);
  const savePersonDataHandler = (enteredPersonData) => {
    const personData = {
      ...enteredPersonData,
      id: Math.random().toString(),
    };
    props.onAddPerson(personData);
    setIsEditing(false);
  };
  const startEditingHandler = () => {
    setIsEditing(true);
  };

  const stopEditingHandler = () => {
    setIsEditing(false);
  };
  return (
    <div>
    {!isEditing && (
      <button className={classes.addPerson} onClick={startEditingHandler}>Contact Us</button>
    )}
    {isEditing && (
      <ContactForm
        onSavePersonData={savePersonDataHandler}
        onCancel={stopEditingHandler}
      />
    )}
  </div>
  )
}

export default NewContact