import React, { Fragment, useRef } from 'react';

import classes from './ContactForm.module.css';

function ContactForm(props) {
  const nameRef = useRef('');
  const emailIdRef = useRef('');
  const contactNumberRef = useRef('');

  function submitHandler(event) {
    event.preventDefault();

  

    const personData = {
      name: nameRef.current.value,
      emailId: emailIdRef.current.value,
      contactNumber: contactNumberRef.current.value,
    };

    props.onSavePersonData(personData);
   
  }

  return (
    <Fragment>
    <form onSubmit={submitHandler}>
      <div className={classes.top}>
      <div className={classes.control}>
        <label htmlFor='name'>Name:</label>
        <input type='text' id='name' ref={nameRef} />
      </div>
      <div className={classes.control}>
        <label htmlFor='emailId'>Email-Id:</label>
        <input type='email'  id='emailId' ref={emailIdRef}></input>
      </div>
      <div className={classes.control}>
        <label htmlFor='contactNumber'>Contact Number:</label>
        <input type='phone' id='contact' ref={contactNumberRef} />
      </div>
      </div>
      <button>Submit</button>
      <button className={classes.cancel} onClick={props.onCancel}>Cancel</button>
      
    </form>
    </Fragment>
  );
}

export default ContactForm;
